.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-midnight-express, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;

    @include vp-767 {
      padding: 60px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    width: 600px;
    padding: 40px;

    font-size: 18px;
    line-height: 1.2;

    border-radius: 5px;
    background-color: $color-default-white;
    color: $color-midnight-express;

    @include vp-767 {
      width: 100%;
    }
  }

  &__close-btn {
    position: absolute;
    top: 22px;
    right: 22px;
    z-index: 1;

    width: 40px;
    height: 40px;
    padding: 0;

    background: $color-transparent;
    border: none;
    cursor: pointer;

    transition: opacity $trans-default;

    &:hover,
    &:focus {
      opacity: 0.6;
    }

    &:active {
      opacity: 1;
    }
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include vp-767 {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    transition: none;
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }

  &.js-success .modal__description {
    text-align: center;
  }

  &.js-success .modal__content {
    display: flex;
    flex-direction: column;
  }

  &.js-success .btn {
    width: 250px;
    margin: 0 auto;

    transition:
      opacity $trans-default,
      color $trans-default,
      border-color $trans-default,
      background-color $trans-default;

    &:hover,
    &:focus {
      color: $color-midnight-express;
      border-color: $color-midnight-express;
      background-color: rgba($color-midnight-express, 0.2);
    }

    &:active {
      opacity: 0.6;
    }
  }

  &.js-error-modal .modal__description {
    color: $color-torch-red;
    text-align: center;
  }
}
