.custom-toggle {
  position: relative;
  font-size: 18px;
  line-height: 27px;
  font-weight: 300;

  display: flex;
  padding: 14px 0;

  &:last-of-type {
    padding: 14px 0 0 0;
  }

  @media screen and (max-width: 374px) {
    font-size: 16px;
    line-height: 1.2;
  }

  input {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  label {
    position: relative;

    padding-left: 52px;

    cursor: pointer;

    span {
      margin-bottom: 0;
      margin-left: 0;
    }
  }

  &--checkbox {
    .custom-toggle__icon {
      position: absolute;
      top: 50%;
      left: 0;

      transform: translateY(-50%);

      width: 28px;
      height: 28px;

      border: 1.5px solid rgba($color-default-white, 0.2);

      transition: border 0.3s ease;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;

        width: 10px;
        height: 10px;

        background-color: transparent;

        transform: translate(-50%, -50%);

        transition: background-color 0.3s ease;
      }
    }
  }

  &--checkbox-agree {
    &:last-of-type {
      padding: 0;
    }

    .custom-toggle__icon {
      top: 0;
      padding: 0;
      transform: translateY(25%);
    }
  }

  label.disabled {
    opacity: 0.3;
    cursor: initial;
  }

  &--radio {
    .custom-toggle__icon {
      position: absolute;
      top: 0;
      left: 0;

      width: 28px;
      height: 28px;

      border: 1px solid rgba($color-default-white, 0.2);
      border-radius: 50%;

      transition: border 0.3s ease;

      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;

        width: 10px;
        height: 10px;

        color: rgba(255, 255, 255, 0);

        border-radius: 50%;
        transform: translate(-50%, -50%);

        transition: color 0.3s ease;
      }
    }
  }

  input:checked + .custom-toggle__icon {
    border: 1.5px solid $color-whisper;

    &::before {
      background-color: $color-whisper;
    }
  }

  &.is-invalid {
    .custom-toggle__icon {
      border: 1.5px solid $color-tomato;
    }
  }

  .input-message {
    position: absolute;
    right: 0;
    bottom: -5px;

    &.is-invalid {
      color: #ff0000;
    }

    &.is-valid {
      color: #15ab07;
    }
  }
}
